.section2{
    width:100%;
    background-color: rgba(0,0,0,0.5);
    height:60vh;
    position:relative;
}

.section2::before{
    content:"";
    background:url("https://images.unsplash.com/photo-1605379399642-870262d3d051?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8M3x8cHJvZ3JhbW1pbmd8ZW58MHx8MHx8&auto=format&fit=crop&w=1000&q=60");
    background-size:cover;
    background-position: center top;
    height:100%;
    width:100%;
    position:absolute;
    top:0;
    left:0;
    z-index:-1;
}
.heading{
    width:100%;
    height:100%;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items: center;
}

.section2 h1{
    font-size:2.4rem;
}

.section2 p{
    font-size: 1.4rem;
    text-align:center;
}

@media screen and (max-width:640){
    .section2 h1{
       font-size:2rem;
    }
}