.section_1{
    width:100%;
    height:100vh;
    position:relative;
}

.into-img{
    width:100%;
    height:100%;
    background-size: cover;
    background-position: center;
    object-fit:cover;
}

.section_1::after{
    content:"";
    width:100%;
    height:100%;
    position:absolute;
    top:0;
    left:0;
    background-color:#000;
    opacity:0.8;
}

.section{
    height:100%;
    width:100%;
}
.section .content{
    position: absolute;
    transform:translate(-50%,-50%);
    top:50%;
    left:50%;
    text-align: center;
}
.section .content h1{
    font-size:4rem;
    padding:0.6rem 0 1.5rem;

}

.section .content p{
    font-size: 1.4rem;
    font-weight:200;
    text-transform: uppercase;
}

.content .btn{
    margin: 1rem 0.2rem;
}

@media screen and (max-width:640px)
{
    .section .content h1{
        font-size:3rem;
    
    }
    
    .section .content p{
        font-size: 1.4rem;
        
    } 
}